/* Resets (http://meyerweb.com/eric/tools/css/reset/ | v2.0 | 20110126 | License: none (public domain)) */
/* stylelint-disable-next-line */
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}

/* stylelint-disable-next-line */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}
table th, table td {
  text-align: left;
}

body {
  text-size-adjust: none;
}

/* Box Model */
*,
*:before,
*:after {
  box-sizing: border-box;
}

/* Container */
body {
  /* min-width: (containers) */
  min-width: 1200px;
}

.container {
  margin-left: auto;
  margin-right: auto;
  /* width: (containers) */
  width: 1200px;
}

/* Modifiers */
.container.small {
  /* width: (containers) * 0.75; */
  width: 900px;
}

.container.big {
  /* max-width: (containers) * 1.25; */
  max-width: 1500px;
  /* min-width: (containers); */
  min-width: 1200px;
  width: 100%;
}

/* Grid */
.\31 2u {
  width: 100%;
}

.\31 1u {
  width: 91.6667%;
}

.\31 0u {
  width: 83.3333%;
}

.\39 u {
  width: 75%;
}

.\38 u {
  width: 66.6667%;
}

.\37 u {
  width: 58.3333%;
}

.\36 u {
  width: 50%;
}

.\35 u {
  width: 41.6667%;
}

.\34 u {
  width: 33.3333%;
}

.\33 u {
  width: 25%;
}

.\32 u {
  width: 16.6667%;
}

.\31 u {
  width: 8.3333%;
}

.\-11u {
  margin-left: 91.6667%;
}

.\-10u {
  margin-left: 83.3333%;
}

.\-9u {
  margin-left: 75%;
}

.\-8u {
  margin-left: 66.6667%;
}

.\-7u {
  margin-left: 58.3333%;
}

.\-6u {
  margin-left: 50%;
}

.\-5u {
  margin-left: 41.6667%;
}

.\-4u {
  margin-left: 33.3333%;
}

.\-3u {
  margin-left: 25%;
}

.\-2u {
  margin-left: 16.6667%;
}

.\-1u {
  margin-left: 8.3333%;
}

/* Rows */
.row > * {
  float: left;
  /* padding-left: (gutters) */
  padding-left: 40px;
}

.row:after {
  clear: both;
  content: "";
  display: block;
  height: 0;
}

/*
.row:first-child > * {
    padding-top: 0 !important;
}
*/
/* Normal */
.row + .row > * {
  /* padding: (gutters) 0 0 (gutters) */
  padding: 40px 0 0 40px;
}

.row {
  /* margin-left: -(gutters) */
  margin-left: -40px;
}

/* Flush */
.row.flush > * {
  padding-left: 0;
}

.row + .row.flush > * {
  padding: 0;
}

.row.flush {
  margin-left: 0;
}

/* Quarter */
.row.quarter > * {
  /* padding-left: (gutters * 0.25) */
  padding-left: 10px;
}

.row + .row.quarter > * {
  /* padding: (gutters * 0.25) 0 0 (gutters * 0.25) */
  padding: 10px 0 0 10px;
}

.row.quarter {
  /* margin-left: -(gutters * 0.25) */
  margin-left: -10px;
}

/* Half */
.row.half > * {
  /* padding-left: (gutters * 0.5) */
  padding-left: 20px;
}

.row + .row.half > * {
  /* padding: (gutters * 0.5) 0 0 (gutters * 0.5) */
  padding: 20px 0 0 20px;
}

.row.half {
  /* margin-left: -(gutters * 0.5) */
  margin-left: -20px;
}

/* One and (a) Half */
.row.oneandhalf > * {
  /* padding-left: (gutters * 1.5) */
  padding-left: 60px;
}

.row + .row.oneandhalf > * {
  /* padding: (gutters * 1.5) 0 0 (gutters * 1.5) */
  padding: 60px 0 0 60px;
}

.row.oneandhalf {
  /* margin-left: -(gutters * 1.5) */
  margin-left: -60px;
}

/* Double */
.row.double > * {
  /* padding-left: (gutters * 2) */
  padding-left: 80px;
}

.row + .row.double > * {
  /* padding: (gutters * 2) 0 0 (gutters * 2) */
  padding: 80px 0 0 80px;
}

.row.double {
  /* margin-left: -(gutters * 2) */
  margin-left: -80px;
}

/* Basic */
body {
  background: transparent;
}

body.loading * {
  animation: none !important;
  transition: none !important;
}

body, input, select, textarea {
  color: #1f1f1f;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
}

body, p, li, input, select, textarea {
  font-size: 14pt;
  line-height: 1.65em;
}

h1, h2, h3, h4, h5, h6 {
  color: inherit;
  font-weight: 700;
  line-height: 1.4em;
  margin-bottom: 1em;
}

h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1.25em;
}

a {
  color: #0000ee;
  text-decoration: underline;
}

h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
  color: inherit;
  text-decoration: none;
}

a:visited {
  color: #551a8b;
}

a.color-link-text,
.color-link-text a {
  color: #1f1f1f;
}

a.color-link-secondary,
.color-link-secondary a {
  color: #eeeeee;
}

strong, b {
  font-weight: 700;
}

em, i {
  font-style: italic;
}

p {
  line-height: 1.5em;
  margin-bottom: 1em;
}

p.medium {
  font-size: 1.4em;
}

sub {
  font-size: 0.8em;
  position: relative;
  top: 0.5em;
}

sup {
  font-size: 0.8em;
  position: relative;
  top: -0.5em;
}

hr {
  border: 0;
  border-top: solid 1px #1f1f1f;
  margin-bottom: 1.5em;
}

/* Tables */
dl, table, table.default {
  margin: 0 0 2em;
}

table td, table th {
  padding: 0 1em 1em 0;
}

table.default tbody tr {
  border-bottom: solid 1px #26626c;
}

table.default td {
  padding: 0.5em 1em;
}

table.default th {
  font-weight: 700;
  padding: 0.5em 1em;
  text-align: left;
}

table.default thead {
  background-color: #26626c;
  color: #eeeeee;
}

section.special, article.special {
  text-align: center;
}

header.major {
  text-align: center;
  text-transform: uppercase;
}

header.major h2 {
  font-size: 2.6em;
  font-weight: 700;
}

header.major .byline {
  display: block;
  font-size: 1.4em;
  letter-spacing: 1px;
  line-height: 1.4em;
  margin-bottom: 2em;
}

footer > :last-child {
  margin-bottom: 0;
}

footer.major {
  padding-top: 3em;
}

/* Form */
input[type=email],
input[type=password],
input[type=text],
textarea {
  appearance: none;
  background: none;
  border: solid 1px #26626c;
  color: inherit;
  display: block;
  outline: 0;
  padding: 0.75em;
  text-decoration: none;
  width: 100%;
}

input[type=text]:focus,
input[type=password]:focus,
input[type=email]:focus,
textarea:focus {
  border-color: #26626c;
}

input[type=email],
input[type=password],
input[type=text] {
  line-height: 1em;
}

:input-placeholder,
::input-placeholder,
:placeholder,
::placeholder {
  color: inherit;
  opacity: 0.5;
}

::input-placeholder {
  position: relative;
  top: 3px;
}

.formerize-placeholder {
  color: rgba(85, 85, 85, 0.5) !important;
}

/* Image */
.image {
  border: 0;
  position: relative;
}

.image.fit {
  display: block;
}

.image.fit img {
  display: block;
  width: 100%;
}

.image.feature {
  display: block;
  margin: 0 0 2em;
}

.image.feature img {
  display: block;
  width: 100%;
}

/* Icon */
.icon {
  position: relative;
}

.icon:before {
  content: "";
  /* stylelint-disable-next-line font-family-no-missing-generic-family-keyword */
  font-family: FontAwesome;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-style: normal;
  font-weight: normal;
  text-transform: none !important;
}

.button.icon:before {
  margin-right: 0.5em;
}

.icon > .label {
  display: none;
}

/* Lists */
ul,
ol {
  list-style-position: outside;
  padding: 0;
}

ul,
ul.default {
  list-style-type: disc;
  margin: 0 0 1em 1.23em;
}

ol,
ol.default {
  list-style-type: decimal;
  margin: 0 0 1em 1.1em;
}

ul li,
ol li,
ul.default li,
ol.default li {
  padding-bottom: 0.75em;
  padding-left: 0.35em;
}

ul ul,
ol ol {
  margin-bottom: 0;
  padding-bottom: 0;
}

ul ul li,
ol ol li {
  padding-bottom: 0;
}

ul ul li {
  list-style-type: circle;
}

ol ol li {
  list-style-type: lower-latin;
}

.alt ul ul,
.alt ol ol,
.sitemap ul ul,
.sitemap ol ol {
  margin-top: 1em;
}

.alt ul li,
.alt ul li li,
.sitemap ul li,
.sitemap ul li li {
  list-style-type: disc;
  margin-bottom: 0;
  margin-top: 0;
  padding-bottom: 1em;
  padding-top: 0;
}
.alt ul li:last-child,
.alt ul li li:last-child,
.sitemap ul li:last-child,
.sitemap ul li li:last-child {
  padding-bottom: 0;
}

ul.actions,
ul.icons,
ul.special-icons {
  cursor: default;
  list-style-type: none;
  margin: 0;
  margin-bottom: 2em;
}

ul.icons li {
  display: inline-block;
  line-height: 1em;
  padding: 0.5em 1em;
}

ul.icons li span {
  display: none;
}

ul.icons li a {
  color: inherit;
  font-size: 2em;
  opacity: 0.35;
  text-decoration: none;
  transition: all 0.25s ease-in-out;
}

ul.icons li a:hover {
  color: inherit;
  opacity: 1;
}

ul.actions li {
  display: inline-block;
}

ul.actions li:first-child {
  padding: 0;
}

ul.actions:last-child {
  margin-bottom: 0;
}

ul.actions.vertical li {
  display: block;
  padding: 1.5em 0 0;
}

ul.actions.vertical li:first-child {
  padding: 0;
}

ul.special-icons {
  margin: 0;
  padding: 0 0 0 1.35em;
}

ul.special-icons li {
  margin-bottom: 1em;
  padding: 0;
  padding-left: 1.4em;
  position: relative;
  vertical-align: top;
  width: 100%;
}

ul.special-icons li:before {
  border-radius: 5px;
  display: block;
  font-size: 2em;
  left: 0;
  position: absolute;
  top: 2em;
}

ul.special-icons span {
  display: inline-block;
  font-size: 1.35em;
  left: -0.5em;
  line-height: 190%;
  overflow: hidden;
  position: absolute;
  top: -0.28em;
  width: 1.4em;
}

ul.special-icons h3 {
  font-size: 1.2em;
  margin-bottom: 1em;
}

ul.special-icons h3,
ul.special-icons h3 a,
ul.special-icons h3 a:visited {
  color: #1f1f1f;
  display: block;
  font-weight: 700;
  text-transform: uppercase;
}

ul.special-icons p {
  margin: 0;
}

/* Button */
input[type=submit],
input[type=reset],
input[type=button],
.button {
  appearance: none;
  background: none;
  border: 2px solid;
  border-color: #26626c;
  border-radius: none;
  color: #26626c;
  cursor: pointer;
  display: inline-block;
  font-size: 1.4em;
  font-weight: 300;
  letter-spacing: 1px;
  padding: 0.9em 1.2em;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.35s ease-in-out;
}

input[type=submit]:visited,
input[type=reset]:visited,
input[type=button]:visited,
.button:visited {
  color: #26626c;
}

input[type=submit]:hover,
input[type=reset]:hover,
input[type=button]:hover,
.button:hover,
.button:active {
  background-color: #26626c;
  color: #eeeeee;
}

input[type=submit].alt,
input[type=reset].alt,
input[type=button].alt,
.button.alt {
  border-color: inherit;
  color: inherit;
}

input[type=submit].alt:hover,
input[type=reset].alt:hover,
input[type=button].alt:hover,
.button.alt:hover {
  background-color: #eeeeee;
  color: #26626c;
}

input[type=submit].fit,
input[type=reset].fit,
input[type=button].fit,
.button.fit {
  width: 100%;
}

.button.button-secondary,
.button.button-secondary:visited {
  border-color: #6f6f6f;
  color: #eeeeee;
  opacity: 0.6;
}

.button.button-secondary:hover,
.button.button-secondary:active {
  background-color: #eeeeee;
  color: #222222;
  opacity: 1;
}

/* Wrapper */
.wrapper {
  padding: 4em 0;
}

.wrapper.style1 {
  padding: 0;
}

.wrapper.style1 #header h1 {
  margin: 0;
}

.wrapper.style2,
.wrapper.evenpart {
  background-color: #eeeeee;
}

.wrapper.evenpart {
  padding-bottom: 4rem;
}

.wrapper.style2 .major,
.wrapper.evenpart .major {
  text-align: left !important;
}

.wrapper.style2 .major h2,
.wrapper.evenpart .major h2 {
  display: block;
  font-size: 1.8em;
  font-weight: 400;
  letter-spacing: 1px;
  margin-bottom: 0.7em;
  text-transform: uppercase;
}

.wrapper.style2 .major .byline,
.wrapper.evenpart .major .byline {
  font-size: 1.4em;
  letter-spacing: normal;
  line-height: 1.4em;
  text-transform: none;
}

.wrapper.style2 h3,
.wrapper.evenpart h3 {
  display: block;
  font-size: 1.6em;
  font-weight: 400;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.wrapper.style3,
.wrapper.cta {
  background-color: #26626c;
  color: #eeeeee;
  padding-bottom: 5em;
  text-align: center;
}

.wrapper.style3 .container,
.wrapper.cta .container {
  padding-left: 6em;
  padding-right: 6em;
}

.wrapper.style3 p,
.wrapper.cta p {
  font-size: 1.6em;
}

/* Sidebar */
.wrapper.style4,
.wrapper.plain {
  background-color: #eeeeee;
}

.wrapper.style5,
.wrapper.portals {
  background-color: #26626c;
  color: #eeeeee;
  text-align: center;
}

.wrapper.style5 .image,
.wrapper.portals .image {
  display: block;
  margin: 0 auto 2em;
  width: 60%;
}

.wrapper.style5 .image img,
.wrapper.portals .image img {
  border: 10px solid;
  border-color: #eeeeee;
  border-radius: 50%;
}

/* Header */
#header {
  background-color: #000000;
  color: #f2f2f2;
  height: 4em;
  width: 100%;
}

#header .container {
  position: relative;
}

#logo {
  color: #f2f2f2;
  font-size: 2em;
  font-weight: 400;
  letter-spacing: 2px;
  line-height: 2em;
  text-transform: uppercase;
}

#logo h1 {
  display: inline-block;
  margin: 0;
  padding: 0;
}

#nav {
  position: absolute;
  right: 0;
  top: 0;
}

#nav > ul {
  margin: 0;
}

#nav > ul > li {
  border-radius: 4px;
  display: inline-block;
  margin-left: 0.5em;
  padding: 0 0.5em;
}

#nav > ul > li a {
  color: inherit;
  font-size: 1em;
  font-weight: 400;
  letter-spacing: 2px;
  line-height: 4em;
  text-decoration: none;
  text-transform: uppercase;
}

#nav > ul > li:first-child {
  margin-left: 0;
}

#nav > ul > li.active a {
  color: #eeeeee;
}

#nav > ul > li > ul {
  display: none;
}

/* Dropotron */
.dropotron {
  background-color: #000000;
  border-radius: 4px;
  color: #eeeeee;
  list-style-type: none !important;
  margin: 0 0 0 -3rem;
  min-width: 20rem;
  padding: 0 0 0.5rem;
  top: 2em;
}

.dropotron > li {
  border-top: solid 1px #262626;
  line-height: 1.5em;
  list-style-type: none !important;
  padding: 0.6rem 1rem;
}

.dropotron > li:first-child {
  border-top: solid 1px transparent;
}

.dropotron > li > a {
  color: inherit;
  letter-spacing: 1px;
  text-decoration: none;
  text-transform: none;
}

.dropotron > li.active > a, .dropotron > li:hover > a {
  color: inherit;
}

.dropotron.level-0,
.dropotron.level-1 {
  border-radius: 0 0 4px 4px;
  font-size: 1em;
}

/* Banner */
#banner {
  background-color: #9e9d9d;
  /* background-position will be overwritten by yaml, if set */
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #f2f2f2;
  height: 24em;
  overflow: hidden;
}

#banner .container h2 {
  background-color: #26626c;
  display: inline-block;
  font-size: 2.4em;
  font-weight: 400;
  line-height: 0.5em;
  margin: 0;
  padding: 0.5em 0.3em;
  text-transform: uppercase;
}

#banner .container .follower, #banner .container .blurb {
  font-size: 1.2em;
  font-weight: 300;
  letter-spacing: 1px;
  margin: 1em 0;
  padding: 0.25em 1.3em;
}

#banner .container .follower {
  display: inline-block;
}

#banner .container .blurb {
  display: inline-block;
}

/* Main */
#main {
  padding: 4em 0;
}

#main #content .major, #main #sidebar .major {
  text-align: left;
}

#main #sidebar section {
  margin-top: 3em;
}

#main #sidebar section:first-child {
  margin-top: 0;
}

#main #sidebar .major {
  padding-top: 0.35em;
}

#main #sidebar .major h2 {
  font-size: 1.8em;
}

/* Extra */
#extra h2 {
  display: block;
  font-size: 1.8em;
  font-weight: 400;
  letter-spacing: 1px;
  text-transform: uppercase;
}

#extra h3 {
  display: block;
  font-size: 1.6em;
  font-weight: 400;
  letter-spacing: 1px;
  text-transform: uppercase;
}

/* Footer */
#footer {
  background-color: #222222;
  color: #eeeeee;
  padding: 6em 0 2em;
  text-align: center;
}

#footer hr {
  border-bottom: 1px solid;
  border-color: inherit;
  opacity: 0.05;
}

#footer .major h2 {
  color: inherit;
}

#footer .major .byline,
#copyright {
  color: inherit;
  opacity: 0.6;
}

#footer .copyright {
  margin-top: 3em;
  text-align: center;
}

/* Copyright */
#copyright {
  padding: 2em 0;
  position: relative;
  text-align: center;
  text-transform: none;
}

#copyright a {
  color: inherit;
  text-decoration: none;
}

/* Team */
#team h3 {
  font-weight: 700;
  letter-spacing: 2px;
  margin-bottom: 0.5em;
  text-transform: uppercase;
}

/* COLORS */
.color-primary {
  color: #26626c;
}

.color-secondary {
  color: #eeeeee;
}

.color-text {
  color: #1f1f1f;
}

/* grey */
.color-disabled {
  color: #9e9d9d;
}

/* green */
.color-success {
  color: #14a44d;
}

/* yellow */
.color-warning {
  color: #e4a11b;
}

/* red */
.color-danger {
  color: #dc4c64;
}

/* blue */
.color-info {
  color: #54b4d3;
}

/* TYPOGRAPHY */
.uppercase {
  text-transform: uppercase !important;
}

.normal-case {
  text-transform: none !important;
}

.text-center {
  text-align: center;
}

.bold {
  font-weight: 700 !important;
}

.italic {
  font-style: italic !important;
}

.small {
  font-size: 80%;
  line-height: 1.5em;
}

.normal {
  font-size: 1.4em;
}

.nowrap {
  white-space: nowrap;
}

.skew {
  margin-top: -0.5rem !important;
  transform: rotate(-3deg);
}

/* LISTS */
.list-none {
  list-style-type: none !important;
  margin-left: 0 !important;
}
.list-none li {
  padding-left: 0 !important;
}

.list-disc {
  list-style-type: disc !important;
}

.list-decimal {
  list-style-type: decimal !important;
}

/* TRANSPARENT BACKGROUND OVERLAY */
.opacity-dark {
  background-color: rgba(0, 0, 0, 0.8);
  color: #f2f2f2;
}

.opacity-light {
  background-color: rgba(255, 255, 255, 0.8);
  color: #1f1f1f;
}

/* MARGIN */
.mb-1 {
  margin-bottom: 1rem !important;
}

.mb-2 {
  margin-bottom: 2rem !important;
}

.mb-3 {
  margin-bottom: 3rem !important;
}

.mr-1 {
  margin-right: 1rem !important;
}

.mr-2 {
  margin-right: 2rem !important;
}

.mr-3 {
  margin-right: 3rem !important;
}

/* PADDING */
.pt-1 {
  padding-top: 1rem !important;
}

.pt-2 {
  padding-top: 2rem !important;
}

.pt-3 {
  padding-top: 3rem !important;
}

/* GRID */
.grid-default {
  display: grid;
  grid-gap: 2em;
  grid-template-columns: repeat(3, 1fr);
}

/* ALIGN */
.relative {
  position: relative;
}

.width-100 {
  width: 100%;
}

.height-100 {
  height: 100%;
}

.center-vh {
  left: 50%;
  margin: 0;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

.center-v {
  margin: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

/* BLOCKQUOTE */
blockquote {
  border-left: solid 0.5em #26626c;
  font-style: italic;
  margin: 0 0 2.5em;
  padding: 1em 0 1em 2em;
}

blockquote p {
  margin: 0;
  padding: 0;
}

/* ICONS */
.icons.alt {
  color: #26626c;
}

.icons.icons-all {
  color: #1f1f1f;
}
.icons.icons-all li {
  display: inline-block;
  text-align: center;
}
.icons.icons-all .fa {
  font-size: 1.8em;
}

/* PRE */
code {
  border: solid 1px #1f1f1f;
  font-family: "Courier New", monospace;
  font-size: 0.9em;
  margin: 0 0.25em;
  padding: 0.16em 0.65em;
}

pre {
  font-family: "Courier New", monospace;
  font-size: 0.9rem;
  margin: 0 0 2rem;
  -webkit-overflow-scrolling: touch;
}

pre code {
  display: block;
  line-height: 1.75em;
  overflow-x: auto;
  padding: 1rem 1.5rem;
}

@media (width >= 1281px) {
  .wrapper.style2,
  .wrapper.evenpart {
    padding-top: 5em;
  }
}
/* Basic */
@media (width <= 1280px) {
  #logo {
    display: none;
  }
  #banner {
    height: 22.5em;
  }
  .grid-default {
    grid-template-columns: repeat(1, 1fr);
  }
}
/* Basic */
@media (width <= 840px) {
  #skel-layers-wrapper {
    padding-top: 44px;
  }
  body, p, li, input, select, textarea {
    font-size: 13pt;
    line-height: 1.5em;
  }
  .small {
    font-size: 90%;
  }
  .normal {
    font-size: 1.1em;
  }
  .align-left-md {
    text-align: left;
  }
  header.major h2 {
    font-size: 1.8em;
  }
  header.major .byline {
    font-size: 1.1em;
  }
  .wrapper.style2 p,
  .wrapper.style3 p {
    line-height: 1.4em;
  }
  /* List */
  ul.actions,
  ul.icons,
  ul.special-icons {
    padding-bottom: 0;
  }
  .grid-default {
    grid-template-columns: repeat(1, 1fr);
  }
  /* Button */
  input[type=submit],
  input[type=reset],
  input[type=button],
  .button {
    display: block;
    margin: 0 auto;
    max-width: 350px;
    width: 100%;
  }
  .wrapper.style2 .major h2,
  .wrapper.evenpart .major h2 {
    font-size: 1.4em;
  }
  .wrapper.style2 .major .byline,
  .wrapper.evenpart .major .byline {
    font-size: 1.1em;
  }
  .wrapper.style2 h3,
  .wrapper.evenpart h3 {
    font-size: 1.2em;
  }
  .wrapper.style3 .container,
  .wrapper.cta .container {
    padding-left: 1em;
    padding-right: 1em;
  }
  .wrapper.style3 p,
  .wrapper.cta p {
    /* font-size: 1.1em; */
  }
  .wrapper.style4,
  .wrapper.plain {
    padding-bottom: 2em;
  }
  .wrapper.style5,
  .wrapper.portals {
    padding-bottom: 4em;
  }
  .wrapper.style5 img,
  .wrapper.portals img {
    width: 30%;
  }
  /* Header */
  #header {
    display: none;
  }
  /* Layers */
  #titleBar {
    background-color: #000000;
    height: 44px;
    line-height: 44px;
  }
  #titleBar .title {
    color: #f2f2f2;
    display: block;
    font-size: 1.3em;
    font-weight: 700;
    letter-spacing: 2px;
    line-height: 44px;
    position: relative;
    text-align: center;
    text-transform: uppercase;
    z-index: 1;
  }
  #titleBar .toggle {
    height: 60px;
    left: 0;
    position: absolute;
    top: 0;
    width: 80px;
    z-index: 2;
  }
  #titleBar .toggle:before {
    background: url("../../images/icon_hamburger.svg") center center no-repeat;
    background-size: 28px 28px;
    content: "";
    display: block;
    font-style: normal;
    font-weight: normal;
    height: 44px;
    line-height: 44px;
    text-align: center;
    text-transform: none !important;
    width: 44px;
  }
  #titleBar .toggle:after {
    content: "";
    display: block;
    height: 13px;
    left: 10px;
    opacity: 0.75;
    position: absolute;
    top: 15px;
    width: 20px;
  }
  #navPanel {
    background: #000000;
    /* hack to enable mobile menu scrolling on android chromium */
    border-top: 5px solid transparent;
    -webkit-overflow-scrolling: touch;
  }
  #navPanel .link {
    border-bottom: solid 1px #262626;
    color: #adadad;
    display: block;
    line-height: 1.5rem;
    min-height: 44px;
    padding: 0.6rem;
    text-decoration: none;
  }
  #navPanel .link.depth-0 {
    color: #f2f2f2;
  }
  #navPanel .link.depth-1 {
    padding-left: 1.4rem;
  }
  #navPanel .link.depth-2 {
    padding-left: 2.3rem;
  }
  /* Banner */
  #banner {
    color: #f2f2f2;
    text-align: center;
    text-transform: uppercase;
  }
  #banner h2 {
    font-size: 1.8em;
    padding: 0.5em;
  }
  #banner span, #banner p {
    font-size: 1.1em;
  }
  #banner span {
    padding: 1em 0;
  }
}
/* Basic */
@media (width <= 640px) {
  #banner {
    height: 23.5em;
  }
  header.major h2 {
    font-size: 1.6em;
  }
  header.major .byline {
    font-size: 1.1em;
  }
  .wrapper {
    padding: 4em 0 2em;
  }
  .wrapper.style2,
  .wrapper.evenpart {
    padding-bottom: 3em;
    padding-top: 3em;
  }
  .wrapper.style4 p,
  .wrapper.plain p {
    margin-bottom: 1em;
  }
  .wrapper.style5 img,
  .wrapper.portals img {
    width: 70%;
  }
  /* Lists */
  ul.icons li a {
    font-size: 1.6em;
  }
  ul.special-icons li {
    margin-bottom: 2em;
  }
  .grid-default {
    grid-gap: 0;
    grid-template-columns: repeat(1, 1fr);
  }
  /* Main */
  #main #sidebar .major h2 {
    font-size: 1.6em;
  }
}