@use "_config.scss" as config;

/* Resets (http://meyerweb.com/eric/tools/css/reset/ | v2.0 | 20110126 | License: none (public domain)) */

/* stylelint-disable-next-line */
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
    border: 0;
    font: inherit;
    font-size: 100%;
    margin: 0;
    padding: 0;
    vertical-align: baseline;
}

/* stylelint-disable-next-line */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
    display: block;
}

body {
    line-height: 1;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;

    th, td {
        text-align: left;
    }
}

body {
    text-size-adjust: none;
}

/* Box Model */

*,
*:before,
*:after {
    box-sizing: border-box;
}

/* Container */

body {

    /* min-width: (containers) */
    min-width: 1200px;
}

.container {
    margin-left: auto;
    margin-right: auto;

    /* width: (containers) */
    width: 1200px;
}

/* Modifiers */

.container.small {

    /* width: (containers) * 0.75; */
    width: 900px;
}

.container.big {

    /* max-width: (containers) * 1.25; */
    max-width: 1500px;

    /* min-width: (containers); */
    min-width: 1200px;
    width: 100%;
}

/* Grid */

.\31 2u {
    width: 100%;
}

.\31 1u {
    width: 91.6667%;
}

.\31 0u {
    width: 83.3333%;
}

.\39 u {
    width: 75%;
}

.\38 u {
    width: 66.6667%;
}

.\37 u {
    width: 58.3333%;
}

.\36 u {
    width: 50%;
}

.\35 u {
    width: 41.6667%;
}

.\34 u {
    width: 33.3333%;
}

.\33 u {
    width: 25%;
}

.\32 u {
    width: 16.6667%;
}

.\31 u {
    width: 8.3333%;
}

.\-11u {
    margin-left: 91.6667%;
}

.\-10u {
    margin-left: 83.3333%;
}

.\-9u {
    margin-left: 75%;
}

.\-8u {
    margin-left: 66.6667%;
}

.\-7u {
    margin-left: 58.3333%;
}

.\-6u {
    margin-left: 50%;
}

.\-5u {
    margin-left: 41.6667%;
}

.\-4u {
    margin-left: 33.3333%;
}

.\-3u {
    margin-left: 25%;
}

.\-2u {
    margin-left: 16.6667%;
}

.\-1u {
    margin-left: 8.3333%;
}

/* Rows */

.row > * {
    float: left;

    /* padding-left: (gutters) */
    padding-left: 40px;
}

.row:after {
    clear: both;
    content: "";
    display: block;
    height: 0;
}

/*
.row:first-child > * {
    padding-top: 0 !important;
}
*/

/* Normal */

.row + .row > * {

    /* padding: (gutters) 0 0 (gutters) */
    padding: 40px 0 0 40px;
}

.row {

    /* margin-left: -(gutters) */
    margin-left: -40px;
}

/* Flush */

.row.flush > * {
    padding-left: 0;
}

.row + .row.flush > * {
    padding: 0;
}

.row.flush {
    margin-left: 0;
}

/* Quarter */

.row.quarter > * {

    /* padding-left: (gutters * 0.25) */
    padding-left: 10px;
}

.row + .row.quarter > * {

    /* padding: (gutters * 0.25) 0 0 (gutters * 0.25) */
    padding: 10px 0 0 10px;
}

.row.quarter {

    /* margin-left: -(gutters * 0.25) */
    margin-left: -10px;
}

/* Half */

.row.half > * {

    /* padding-left: (gutters * 0.5) */
    padding-left: 20px;
}

.row + .row.half > * {

    /* padding: (gutters * 0.5) 0 0 (gutters * 0.5) */
    padding: 20px 0 0 20px;
}

.row.half {

    /* margin-left: -(gutters * 0.5) */
    margin-left: -20px;
}

/* One and (a) Half */

.row.oneandhalf > * {

    /* padding-left: (gutters * 1.5) */
    padding-left: 60px;
}

.row + .row.oneandhalf > * {

    /* padding: (gutters * 1.5) 0 0 (gutters * 1.5) */
    padding: 60px 0 0 60px;
}

.row.oneandhalf {

    /* margin-left: -(gutters * 1.5) */
    margin-left: -60px;
}

/* Double */

.row.double > * {

    /* padding-left: (gutters * 2) */
    padding-left: 80px;
}

.row + .row.double > * {

    /* padding: (gutters * 2) 0 0 (gutters * 2) */
    padding: 80px 0 0 80px;
}

.row.double {

    /* margin-left: -(gutters * 2) */
    margin-left: -80px;
}
